<template>
  <div id="app">
    <div class="main">
      <div class="photo">
        <div class="countdown">{{CountDown}}</div>
        <img :src="`${cardData.card}`" class="blitzCard" />
      </div>
      <div class="control">
        <div class="mb-auto">
          <div
            class="answerStyle questionMark"
            v-if="!OpenAnswer"
            @click="OpenAnswer =!OpenAnswer"
          >？</div>
          <img :src="`${answers[cardData.answer].img}`" class="answerStyle" v-if="OpenAnswer" />
        </div>
        <button
          type="button"
          class="btn btn-outline-secondary btn-block btnStyle"
          @click="changeCard"
        >下一題</button>
      </div>
    </div>
  </div>
</template>

<script>
import answers from "./answers";
import cards from "./cards";
export default {
  data() {
    return {
      cardData: {},
      cardNumber: "",
      OpenAnswer: false,
      changeCardSwitch: true,
      CountDown: ""
    };
  },
  computed: {
    answers() {
      return answers;
    },
    cards() {
      return cards;
    }
  },
  methods: {
    changeCard() {
      if (!this.changeCardSwitch) {
        console.log("不給按");
        return;
      }
      let NewCard = Math.floor(Math.random() * 50);
      NewCard == this.cardData.number
        ? this.changeCard()
        : ((this.OpenAnswer = false),
          (this.changeCardSwitch = false),
          (this.cardNumber = NewCard),
          (this.CountDown = 3),
          this.hideCard(),
          this.countDownGo());
    },
    hideCard() {
      let blitzCard = document.querySelector(".blitzCard");
      blitzCard.classList.add("hide");
      this.showCountDown();
      setTimeout(() => {
        this.cardData = this.cards[this.cardNumber];
      }, 1000);
    },
    showCard() {
      let blitzCard = document.querySelector(".blitzCard");
      blitzCard.classList.remove("hide");
      this.hideCountDown();
      this.changeCardSwitch = true;
    },
    showCountDown() {
      let countdown = document.querySelector(".countdown");
      countdown.classList.add("show");
    },
    hideCountDown() {
      let countdown = document.querySelector(".countdown");
      countdown.classList.remove("show");
    },
    countDownGo() {
      let time = setInterval(() => {
        if (this.CountDown !== 1) {
          this.CountDown -= 1;
        } else {
          clearInterval(time);
          this.showCard();
        }
      }, 1000);
    }
  },
  mounted() {
    this.cardData = this.cards[0];
  }
};
</script>

<style lang="scss">
@import "./assets/scss/_reset.scss";
@import "~bootstrap/scss/bootstrap";
@import "./assets/scss/all.scss";
</style>



